/* Section spacing: πόσο απέχει από το banner πάνω και από τα Skills κάτω */
.projects-section {
    

  width: 100%;
  max-width: 1200px;
  margin: 48px auto 56px;   /* ↑ top gap | ↓ bottom gap πριν τα Skills */
  
}

.projects-section h2 {
  font-size: 26px;
  color: white;
  margin-bottom: 24px;
}

/* === Απλώνουμε τις κάρτες με Grid === */
.pj {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
  gap: 24px;
}

/* Η κάρτα γεμίζει το διαθέσιμο πλάτος της στήλης */
.projects-card {
  /* αφαιρούμε τα flex/max-width ώστε να απλώνεται */
  background: #2a2a2a;
  border-radius: 10px;
  padding: 2px;
  text-align: center;
  box-shadow: 0 2px 8px rgba(0,0,0,.3);
  transition: transform .3s ease;
  height: 100%;
}

.projects-card:hover { transform: translateY(-5px); }

.projects-card img {
  width: 100%;
  height: 200px;              /* κρατά το ίδιο ύψος όπως φαίνεται τώρα */
  object-fit: contain;        /* ✅ δείχνει ΟΛΗ την εικόνα χωρίς να κόβεται */
  background-color: #1a1a1a;  /* προαιρετικά: διακριτικό φόντο γύρω από την εικόνα */
  border-radius: 8px;
  display: block;
  margin: 0 auto;
}

.projects-card h3 { margin-top: 12px; font-size: 18px; color: #fff; }
.projects-card p  { margin-top: 6px;  font-size: 14px; color: #ddd; }

/* Breakpoints (προαιρετικά ελαφρύ fine-tune) */
@media (max-width: 600px) {
  .projects-section {
    padding: 0 12px;
  }
  .projects-section h2{
    text-align: center;
  }

  .pj {
    grid-template-columns: 1fr; /* ✅ μία κάρτα ανά σειρά */
    justify-items: center;      /* ✅ κεντραρισμένη σε μικρή οθόνη */
  }

  .projects-card {
    width: 100%;
    max-width: 340px;
  }
}